<template>
  <b-container :fluid="true">
<!--    {{searchResult}}-->
    <b-row>
      <b-col v-for="(product, idx) in products" :key="idx" lg="2" md="3" sm="4" cols="6" class="productClass">
        <product-box :product="product"></product-box>
      </b-col>
    </b-row>
    <hr>
    <b-row class="productClass" v-if="!productLoadingDone" >
      <b-overlay :show="busy" opacity="0.4" spinner-small
                        spinner-variant="primary" class="d-inline block">
        <b-button variant="success" :disabled="busy" @click="loadSearchProducts">
          {{ products.length ? 'Load More' : 'Loading' }} Search Results
        </b-button>
      </b-overlay>
    </b-row>
  </b-container>
</template>

<script>
import ProductBox from '../../components/Boxes/ProductBox'
import api from '../../plugins/axios'
import Fuse from 'fuse.js'
import { mapGetters } from 'vuex'

export default {
  name: 'SearchOnAll',
  data () {
    return {
      products: [],
      productLoadingDone: false,
      searchResult: [],
      searchResultFound: false,
      busy: false
    }
  },
  computed: {
    ...mapGetters([
      'searchProducts',
      'searchProductsLoaded'
    ])
  },
  components: {
    ProductBox
  },
  methods: {
    loadSearchResults () {
      if (!this.searchResultFound) {
        const fuse = new Fuse(this.searchProducts, { keys: ['name'] })
        this.searchResult = fuse.search(this.$route.query.string).map(product => product.item.id)
        this.searchResultFound = true
      }
    },
    loadSearchProducts () {
      this.loadSearchResults()
      this.busy = true
      const start = this.products.length
      let end = start + parseInt(process.env.VUE_APP_SEARCH_LIMIT)
      let clip = false
      if (end > this.searchResult.length) {
        clip = true
        end = this.searchResult.length
      }
      const productIds = this.searchResult.slice(start, end)
      api.post('/search', { productIds })
        .then(response => {
          console.log('Search products loaded')
          console.log(response)
          this.products = this.products.concat(response.data.products)
          if (clip) {
            this.productLoadingDone = true
          }
          this.busy = false
        })
        .catch(error => {
          console.log('Search products loading failed')
          console.log(error.response)
        })
    },
    initialLoader () {
      this.searchResultFound = false
      this.products = []
      this.productLoadingDone = false
      if (this.searchProductsLoaded) {
        this.loadSearchProducts()
      } else {
        const inCreated = setInterval(() => {
          if (this.searchProductsLoaded) {
            this.loadSearchProducts()
            clearInterval(inCreated)
          }
        }, process.env.VUE_APP_RETRY_INTERVAL)
      }
    }
  },
  watch: {
    '$route' (to, from) {
      this.initialLoader()
    }
  },
  created () {
    this.initialLoader()
  }
}
</script>

<style scoped>
.productClass {
  padding: 0.5%;
}
</style>
